import {
  Box,
  Button,
  Flex,
  Input,
  Heading,
  HStack,
  Spacer,
  Text,
  VStack,
  Link,
  Image,
  Img,
  Select,
  FormControl,
  FormLabel,
  FormHelperText,
  FormErrorMessage,
  UnorderedList,
  ListItem,
  OrderedList,
  Textarea,
} from '@chakra-ui/react'
import { useEffect, useState } from 'react'
import './style.css'
import axios from 'axios'
import swal from 'sweetalert'

const Home = ({ scrollTo, LiveDemoRef }) => {
  const [dash, getDashboard] = useState([])
  const [email, setEmail] = useState('')
  const [dashType, setDashType] = useState('')
  const [connectorUrl, setConnectorUrl] = useState('')
  const [remark, setRemark] = useState('')
  const [accessToken, setAccessToken] = useState('')
  const [isLoading, setIsLoading] = useState(false)
  const [isValid, setIsValid] = useState(true)
  const [isLoadingDemo, setIsLoadingDemo] = useState(false)

  useEffect(() => {
    getAllDashboards()
  }, [])

  // const baseUrl = 'https://odoo.techneith.com'
  const getAllDashboards = () => {
    const url = 'https://odoo.techneith.com/techneith/dashboards/'
    axios.get(`${url}`).then(response => {
      const allDashboard = response.data

      getDashboard(allDashboard)
    })
  }
  const showAlert = () => {
    swal({
      title: 'Success',
      text: 'We have received your request. You will get a response within 24hrs',
      icon: 'success',
      button: 'OK',
    }).then(function () {
      window.location.href = '/'
    })
  }
  const formSubmission = async () => {
    setIsLoadingDemo(true)

    const formData = {
      //used to send data in formData which is being received in the- form api
      staging_url: connectorUrl,
      token: accessToken,
      email: email,
      dashboard: dashType,
      remark: remark,
    }

    var postData = new FormData()
    postData.append('staging_url', formData.staging_url)
    postData.append('token', formData.token)
    postData.append('email', formData.email)
    postData.append('dashboard', formData.dashboard)
    postData.append('remark', formData.remark)

    try {
      const res = await fetch(
        'https://odoo.techneith.com/request/dashboard-demo/',
        {
          method: 'POST',
          mode: 'cors',
          body: postData,
        },
      )

      const response = res.json()
      console.log('Final Response: ', response)
      setIsLoadingDemo(false)
      setIsLoading(false)
      showAlert()
    } catch (error) {
      console.log('Error: ', error)
    }
  }

  const checkValidation = async event => {
    event.preventDefault() //to not to refresh or redirecting to new page
    setIsLoading(true) //
    const values = {
      url: connectorUrl,
      dashType: dashType,
      token: accessToken,
      email: email,
    }

    const validationData = {
      url: connectorUrl,
      token: accessToken,
    }
    var bodyFormData = new FormData()
    bodyFormData.append('url', validationData.url)
    bodyFormData.append('token', validationData.token)
    try {
      const res = await fetch('https://odoo.techneith.com/authorize/customer', {
        method: 'POST',
        mode: 'cors',
        body: bodyFormData,
      })

      const response = await res.json()
      console.log('Response1: ', res.status === 200)
      setIsLoading(false)
      setIsValid(true)
      console.log('isvalid:', isValid)
      if (res.status == 200) {
        console.log('Success, waiting call for formSubmission')

        await formSubmission()
      } else {
        setIsValid(false)
      }
    } catch (error) {
      setIsValid(false)
      console.log(error)
    }

    // axios
    //   .post(url, {
    //     url: 'https://development.techneith.com/power-bi/connecter/',
    //     token: '_f7417e458050a91a968ff1772bb343d5c5739335',
    //   })
    //   .then(
    //     response => {
    //       console.log('Response: ', response.data)
    //     },
    //     error => {
    //       console.log('Error: ', error)
    //     },
    //   )
    console.log('Form Values: ', values)
    //
  }

  return (
    <Flex
      h={{ base: '1100', sm: '1100', md: '1100px', lg: '900px' }}
      w="100%"
      justifyContent="center"
      alignItems="center"
      px={{ base: '5', md: '10', lg: '100px', xl: '10%' }}
      position="relative"
    >
      <Flex
        h="full"
        flexDirection={{
          base: 'column',
          md: 'column',
          lg: 'column',
          xl: 'row',
        }}
        w={{ base: 'full', md: 'full', lg: 'full', xl: '90%' }}
        justifyContent="center"
        alignItems="center"
        pt="20"
      >
        <Box
          position="relative"
          display="flex"
          alignItems="start"
          justifyContent={{ base: 'center', xl: 'start' }}
          w={{ base: 'full', md: 'full', lg: 'full', xl: '50%' }}
          h={{ base: 'full', md: 'full', lg: 'full' }}
          pt={{ base: '20' }}
          pr={{ base: '5', md: '0' }}
        >
          <VStack
            bg="none"
            w={{ base: '310px', md: '510px', lg: '490px', xl: '590px' }}
            h={{ base: '180px', md: '350px', lg: '300px', xl: '280px' }}
            spacing="none"
            borderRadius="5px"
          >
            <Box bg="none" w="full" h="full" borderRadius="0 0 5px 5px">
              <Img src="https://user-images.githubusercontent.com/68636367/202851767-11008859-b92f-4c1a-a3b7-5f1ea664ccbd.gif" />
            </Box>
          </VStack>
        </Box>
        <Spacer />
        <VStack
          alignItems={{ base: 'center', xl: 'center' }}
          justifyContent={{ base: 'end', xl: 'center' }}
          w={{ base: 'full', md: 'full', lg: 'full', xl: '50%' }}
          h={{ base: 'full', md: 'full', lg: 'full' }}
          p="6"
        >
          <Heading
            as="h1"
            fontSize={{ base: '2xl', md: '3xl', lg: '3xl' }}
            align="center"
          >
            Test Dashboard
          </Heading>
          <Heading
            as="h2"
            fontSize={{ base: 'lg', md: 'xl', lg: '2xl' }}
            fontWeight="small"
            align="center"
          >
            Request Live Demo on your Odoo data
          </Heading>
          <form onSubmit={checkValidation}>
            <FormControl>
              <VStack
                alignItems={{ base: 'center', md: 'end', lg: 'center' }}
                justifyContent={{ base: 'center', xl: 'center' }}
                w={{ base: 'full', md: 'full', lg: 'full', xl: '100%' }}
                h={{ base: '60%', md: '50%', lg: 'full' }}
                pt="6"
                spacing="4"
              >
                <Input
                  variant="filled"
                  placeholder="Your Email"
                  type="email"
                  onChange={event => setEmail(event.currentTarget.value)}
                  isRequired
                />

                <Select
                  variant="filled"
                  placeholder="Select Dashboard"
                  onChange={event => setDashType(event.currentTarget.value)}
                  isRequired
                  mt="5"
                >
                  {dash.map(dashboard => (
                    <option value={dashboard.id} key={dashboard.id}>
                      {dashboard.name}
                    </option>
                  ))}
                </Select>

                <Input
                  variant="filled"
                  placeholder="Connector Url from Power-bi Direct Connector settings"
                  onChange={event => setConnectorUrl(event.currentTarget.value)}
                  isRequired
                />
                <Input
                  variant="filled"
                  placeholder="Access Token from Power-bi Direct Connector settings"
                  onChange={event => setAccessToken(event.currentTarget.value)}
                  required
                />
                <Textarea
                  placeholder="Add your remarks/message"
                  variant="filled"
                  onChange={event => setRemark(event.currentTarget.value)}
                />

                {isValid ? (
                  <FormHelperText>Enter the details for demo.</FormHelperText>
                ) : (
                  <Text color="red.500">*Invalid Url or Token</Text>
                )}
                <HStack>
                  {isLoadingDemo ? (
                    <Image
                      boxSize="120px"
                      src="https://user-images.githubusercontent.com/68636367/202997455-fcbf07e4-4ff4-4e5b-9ea4-280adb6a7af9.gif"
                    />
                  ) : (
                    <Button
                      className="buttonSubmit"
                      px="4"
                      colorScheme="blue"
                      m="6px"
                      type="submit"
                    >
                      Submit
                    </Button>
                  )}
                </HStack>

                <OrderedList pt="10">
                  <ListItem>
                    Enter the Url and Token from your{' '}
                    <b>Power-Bi Direct Connector</b> settings in Odoo
                  </ListItem>
                  <ListItem>
                    We will send Your Demo Dashboard Url to mentioned email
                    within one working day
                  </ListItem>
                  <ListItem>
                    Advised to regenerate connector token after the demo expires
                  </ListItem>
                  <ListItem>
                    This demo only works with{' '}
                    <b>Techneith's Power-Bi Direct Connector</b> because it is
                    only direct connector available from odoo to power-bi
                  </ListItem>
                  <ListItem>
                    For Power-BI Dashboards development and customizations
                    contact us on <b>info@techneith.com</b>
                  </ListItem>
                </OrderedList>
              </VStack>
            </FormControl>
          </form>
        </VStack>
      </Flex>
    </Flex>
  )
}
export default Home
